import { Component, OnInit } from '@angular/core';
import { WerewolfService } from '../werewolf.service';
import { ActivatedRoute } from '@angular/router';
import { CookieService } from "ngx-cookie-service";

@Component({
  selector: 'app-room',
  templateUrl: './room.component.html',
  styleUrls: ['./room.component.scss']
})
export class RoomComponent implements OnInit {

  private room: string;

  constructor(private werewolf: WerewolfService, private route: ActivatedRoute, private cookieService: CookieService) { }


  ngOnInit() {
    this.room = this.route.snapshot.paramMap.get('room');
    console.log(this.room);
    console.log(this.cookieService.get('Test'));
    this.werewolf.messages.subscribe(msg => {
      console.log(msg);
    })
  }

  sendMessage() {
    this.werewolf.sendMsg("test message");
  };

}